/*-----------------------------------------------------------------------------------*/
/* PARAGRAPH */
/*-----------------------------------------------------------------------------------*/

/* --------------------- VARIABLES --------------------- */

$paragraph-margin-top-lg:     2.5rem;
$paragraph-margin-bottom-lg:  2.5rem;
$paragraph-margin-top-sm:     2rem;
$paragraph-margin-bottom-sm:  2rem;


/* --------------------- STYLING --------------------- */

p {
  margin-top: $paragraph-margin-top-sm;
  margin-bottom: $paragraph-margin-bottom-sm;

  @include media-up(md) {
    margin-top: $paragraph-margin-top-lg;
    margin-bottom: $paragraph-margin-bottom-lg;
  }

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }

}


/*-----------------------------------------------------------------------------------*/
/* HEADINGS */
/*-----------------------------------------------------------------------------------*/

/* --------------------- VARIABLES --------------------- */

$heading-margin-top-lg:      3.5rem;
$heading-margin-bottom-lg:   2.5rem;
$heading-margin-top-sm:      2rem;
$heading-margin-bottom-sm:   2rem;


/* --------------------- MIXINS --------------------- */

@mixin h1 {
  @include fluid-type(2.8rem, 3.5rem, 3.8rem);
}

@mixin h2 {
  @include fluid-type(2.8rem, 3.5rem, 3.8rem);
}

@mixin h3 {
  @include fluid-type(2.5rem, 2.8rem, 3rem);
}

@mixin h4 {
  @include fluid-type(2rem, 2.3rem, 2.5rem);
}

@mixin h5 {
  @include fluid-type(1.8rem, 2rem, 2.2rem);
}


/* --------------------- STYLING --------------------- */

h1, h2, h3, h4, h5, h6 {
  font-weight: var(--regular);
  line-height: 1.3;
  color: inherit;
  margin-top: $heading-margin-top-sm;
  margin-bottom: $heading-margin-bottom-sm;

  @include media-up(md) {
    margin-top: $heading-margin-top-lg;
    margin-bottom: $heading-margin-bottom-lg;
  }

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }

  span {
    color: var(--accent);
  }

}

h1 {
  @include h1;
}

h2 {
  @include h2;
}

h3 {
  @include h3;
}

h4 {
  @include h4;
}

h5 {
  @include h5;
}


/*-----------------------------------------------------------------------------------*/
/* LINKS */
/*-----------------------------------------------------------------------------------*/

a {
  color: var(--accent);
  font-weight: var(--bold);
  transition: all 0.2s ease-out;

  @include hover {
    color: var(--accent-light);
  }

}


/*-----------------------------------------------------------------------------------*/
/* LISTS */
/*-----------------------------------------------------------------------------------*/

/* --------------------- VARIABLES --------------------- */

$list-indent:             2rem;
$list-gutter:             0.5rem;
$list-bullet-size:        0.6rem;
$list-bullet-color:       var(--accent);


/* --------------------- STYLING --------------------- */

ul,
ol {
  margin-top: $paragraph-margin-top-sm;
  margin-bottom: $paragraph-margin-bottom-sm;

  @include media-up(sm) {
    margin-top: $paragraph-margin-top-lg;
    margin-bottom: $paragraph-margin-bottom-lg;
  }

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }

  li::marker {
    color: $list-bullet-color;
  }
  
}

@mixin list-bullet {
  list-style: none;
  padding-left: $list-indent;

  li {
    position: relative;
    margin: 0;

    &::marker {
      display: none;
    }

    &:not(:last-child) {
      margin-bottom: $list-gutter;
    }

    &::before {
      position: absolute;
      left: -$list-indent;
      top: 0.9rem;
      display: block;
      content: '';
      width: $list-bullet-size;
      height: $list-bullet-size;
      background: $list-bullet-color;
      border-radius: 50%;
    }
    
  }

}

@mixin list-number {
  padding-left: $list-indent;
  counter-reset: custom-counter;
  list-style: none;

  li {
    position: relative;
    margin: 0;
    counter-increment: custom-counter;

    &::marker {
      display: none;
    }

    &:not(:last-child) {
      margin-bottom: $list-gutter;
    }

    &::before {
      position: absolute;
      left: -$list-indent;
      top: 0;
      display: block;
      content: counter(custom-counter) ".";
      font-weight: var(--bold);
      color: $list-bullet-color;
    }

  }

}

@mixin list-inline {
  list-style: none;
  padding: 0;
  margin: 0;

  li {
    display: inline-block;

    &:not(:first-child) {
      margin-left: $list-indent;
    }

  }

}

.list {

  &--bullet { 
    @include list-bullet;
  }

  &--number { 
    @include list-number;
  }

  &--inline {
    @include list-inline;
  }

}


/*-----------------------------------------------------------------------------------*/
/* BLOCKQUOTE */
/*-----------------------------------------------------------------------------------*/

/* --------------------- VARIABLES --------------------- */

$blockquote-indent:          $gutter-lg;
$blockquote-border-width:    0.5rem;
$blockquote-border-color:    var(--accent);


/* --------------------- STYLING --------------------- */

blockquote {
  border-left: $blockquote-border-width solid $blockquote-border-color;
  padding-left: $blockquote-indent;
  margin-left: 0;
  margin-top: $paragraph-margin-top-sm;
  margin-bottom: $paragraph-margin-bottom-sm;

  @include media-up(sm) {
    margin-top: $paragraph-margin-top-lg;
    margin-bottom: $paragraph-margin-bottom-lg;
  }

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }

}

figure.quote {
  border-left: $blockquote-border-width solid $blockquote-border-color;
  padding-left: $blockquote-indent;
  margin-left: 0;
  margin-top: $paragraph-margin-top-sm;
  margin-bottom: $paragraph-margin-bottom-sm;

  @include media-up(sm) {
    margin-top: $paragraph-margin-top-lg;
    margin-bottom: $paragraph-margin-bottom-lg;
  }

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }

  blockquote {
    border: 0;
    padding: 0;
    margin: 0;
  }

  figcaption {
    margin-top: 1rem;
  }

}